// Style.less для блока main-nav
//-------------------------------

.main-nav {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
}

.main-nav__toggle {
  position: absolute;
  top: -53px;
  right: 6px;
  display: inline-block;
  width: 45px;
  height: 40px;
  border: none;
  background-color: @bg-white;
  cursor: pointer;
}

.main-nav--closed .main-nav__toggle {
  position: relative;
  top: -42px;
  right: -129px;
  display: block;
  width: 25px;
  height: 25px;
  background-image: url("../img/icon-mobile-menu.svg");
  background-repeat: no-repeat;
}

.main-nav--opened .main-nav__toggle {
  position: absolute;
  top: -42px;
  right: 1px;
  display: block;
  width: 18px;
  height: 18px;
  background-image: url("../img/icon-close.svg");
  background-repeat: no-repeat;
}

@media (max-width: @mobile-width-only) {
  .main-nav--closed .site-list__item {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .main-nav {
    display: block;
    margin: 0;
    padding-top: 20px;
  }

  .main-nav--closed .main-nav__toggle {
    display: none;
  }

  .main-nav--opened .main-nav__toggle {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .main-nav {
    display: block;
    margin: 0;
    padding: 0;
    z-index: 1;
  }

  .main-nav__toggle {
    display: none;
  }
}
